.main-products {
    margin-top: 150px;
}
.main-products__titles {
}
.main-products__title {
}
.title__style {
}
.main-products__body {
    padding-top: 44px;  
    width: 1440px;
}
.products__cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    overflow-x: scroll;
    .card__item {
        position: relative;
        width: 412px;
        background: #161616;
        border-radius: 24px;
        margin: 0 10px;
        &:hover {
            background: var(--primary-color);
        }
        .card__text {
            height: 240px;
            .card__title {
                font-weight: 700;
                font-size: 38px;
                color: var(--white-color);
                padding: 44px 32px 0;
            }
            p {
                font-weight: 500;
                font-size: 18px;
                line-height: 140%;
                color: var(--white-color);
                padding: 20px 32px 0;
            }
        }
        .card_btn {
            position: absolute;
            left: 32px;
            bottom: 44px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            background: var(--white-color);
            width: 197px;
            border-radius: 30px;
            padding: 0 0 0 32px;
            a {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: space-between;
                font-weight: 700;
                font-size: 16px;
                color: var(--black-color);
                padding: 19px 0;
                .img__arrow {
                    position: absolute;
                    right: -82px;
                    top: 0;
                    width: 54px;
                }
            }  
            &:hover {
                background: var(--primary-color);
            } 
            &:hover a {
                color: var(--white-color);
            }
        }
    }
}
.products__cards::-webkit-scrollbar {
    display: none;
}